.eltd-author-widget{
    text-align: center;
    .eltd-author-image-holder{
        position: relative;

        @include ipad-landscape{
            display: inline-block;
        }

        img{
            max-width: 100%;
            display: block;
            border-radius: 50%;
            width: 225px;
            height: 225px;

            @media only screen and (min-width: 1025px) {
                margin: 0 auto;
            }

        }
        .eltd-author-title{
            position: absolute;
            padding: 0 6px;
            background-color: $first-main-color;
            font-family: $default-heading-font;
            color: #1c1c1c;
            font-size: 18px;
            font-weight: 200;
            font-style: italic;
            letter-spacing: .08em;
            bottom: 20px;
        }
    }

    .eltd-author-name{
        margin: 33px 0 0;
    }

    .eltd-author-position{
        margin: 3px 0;
    }

    .eltd-author-bio-holder{
        margin-top: 25px;
    }
}