.eltd-page-header{
    .widget_calendar,
    .widget_archive,
    .widget_pages,
    .widget_meta,
    .widget_recent_comments,
    .widget_recent_entries,
    .widget_rss,
    .widget_tag_cloud,
    .widget_categories,
    .widget_nav_menu{
        display: none!important;
    }

    .widget{
        display: inline-block;

        .widgettitle{
            display: none;
        }
    }
}

.eltd-top-bar{
    .widget_calendar,
    .widget_archive,
    .widget_pages,
    .widget_meta,
    .widget_recent_comments,
    .widget_recent_entries,
    .widget_rss,
    .widget_tag_cloud,
    .widget_categories,
    .widget_nav_menu,
    .widget_search{
        display: none!important;
    }

    .widget{
        display: inline-block;

        .widgettitle{
            display: none;
        }
    }
}