/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;
    color: $widget-text-color;

    a{
        color: $widget-link-color;

        &:hover{
            color: $widget-link-hover-color;
        }
    }

    .eltd-widget-title-holder {
        margin-bottom: $widget-inner-padding;

        .eltd-widget-title {
            color: $widget-heading-color;
            margin-bottom: 3px;
        }

        .eltd-separator {
            border-color: $widget-text-color;
            border-bottom-width: 3px;
            width: 30px;
        }
    }

    ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li:not(.eltd-bl-item) {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;
            text-transform: lowercase;
            letter-spacing: .1em;

            &:last-child {
                padding-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            a {
                color: $widget-link-color;
                font-size: 13px;
                font-family: $default-heading-font;
                font-weight: 300;
                font-style: italic;
                text-transform: lowercase;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            margin-top: $widget-list-item-padding;
            padding-top: $widget-list-item-padding;
            padding-left: 3 * $widget-list-item-padding;
        }
    }

    // widgets with lists and animation on link hover
    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {

        .eltd-widget-title {
            margin-bottom: 0;
        }

        ul {

            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                }
            }
        }
    }

    // widgets with lists and w/o animation on link hover
    &.widget_recent_comments,
    &.widget_rss {

        .eltd-widget-title {
            margin-bottom: 0;
        }
    }

    // calendar
    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: $widget-text-color;
            background-color: $widget-list-item-background-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    // rss
    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    // search
    &.widget_search {

        .input-holder {
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-heading-font;
                font-size: 13px;
                font-weight: 300;
                font-style: italic;
                text-transform: lowercase;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include eltdTransition(color .2s ease-in-out);
            }

            input.search-field {
                width: calc(100% - #{$widget-search-form-height});
                padding: 0 0 0 21px;

                @include eltdPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: $widget-list-item-background-color;
                color: $widget-text-color;
                cursor: pointer;
                font-size: 13px;
                width: 52px;
                float: right;

                @include eltdTransition(color .2s ease-in-out);

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    // search post type
    &.eltd-search-post-type-widget {
        .eltd-post-type-search-field {
            border-radius: 3px;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            background-color: transparent;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 12px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            outline: 0;
            position: relative;
            vertical-align: top;
            -webkit-appearance: none;
            padding: 0 25px 0 20px;
            width: 100%; /* width of loading icon */

            @include eltdTransition(color .2s ease-in-out);

            @include eltdPlaceholder {
                color: $widget-text-color;
            }
        }
    }

    // combobox
    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            border-radius: 0;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include eltdTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    // tag cloud
    &.widget_tag_cloud {

        a {
            border-radius: 0;
            color: #393939;
            display: inline-block;
            font-size: 13px !important;
            font-family: $default-heading-font;
            font-weight: 300;
            line-height: $widget-tag-cloud-height;
            text-transform: lowercase;
            font-style: italic;
            margin: 0 7px 7px 0;
            padding: 0 24px;
            background-color: $first-main-color;
            border: 1px solid $first-main-color;
            @include eltdTransition(background-color .2s ease-in-out);

            &:hover {
                background-color: transparent;
            }

            &:after{
                display: none;
            }
        }
    }

    &.eltd-recent-post-widget{
        .eltd-recent-posts{
            .eltd-rp-item{
                margin: 0 0 10px;
                padding: $widget-list-item-padding;

                a{
                    display: table;
                    width: 100%;

                    .eltd-rp-image,
                    .eltd-rp-title{
                        display: table-cell;
                        vertical-align: middle;
                    }

                    .eltd-rp-title{
                        @include eltdTransition(color .2s ease-in-out);
                        font-weight: 600;
                    }

                    .eltd-rp-image{
                        padding-right: 20px;
                        width: 56px;
                        height: 56px;
                        img{
                            display: block;
                        }
                    }

                    &:hover{
                        .eltd-rp-title{
                            color: $default-text-color;
                        }
                    }
                }
            }
        }
    }
}