// widgets spacing
$widget-bottom-margin: 45px;
$widget-inner-padding: 15px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 5px;


// list items color
$widget-list-item-background-color: transparent;

// list items border
$widget-list-item-border-color: #cccccc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #393939;
$widget-heading-color: #000;

// links
$widget-link-color: #393939;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

footer {
    @import 'widgets/default-wordpress-widgets-flat';

    .widget{
        font-family: $default-heading-font;
        font-weight: 300;
        font-size: 13px;

        p{
            margin: 0;
            line-height: 22px;
            font-size: 12px;
            letter-spacing: .15em;
        }

        &.widget_tag_cloud{
            a{
                color: #000;

                &:hover{
                    color: #000;
                }
            }
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    .eltd-social-icon-widget-holder{
        border-color: #ddd;
    }
}