@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltd-comment-holder {
    @include eltdRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltd-comment-holder-inner {
        padding: 42px 0;
    }
	
	.eltd-comment {
		
		&.eltd-pingback-comment {
			
			.eltd-comment-text {
				padding: 0;
			}
		}
	}

    .eltd-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include eltdRelativeHolderLayout();
            margin: 0;
            padding: 0;

            .eltd-comment {
                border-top: 1px solid $default-border-color;
                padding: 39px 0 51px;
            }
        }

        > li:first-child {
            .eltd-comment {
                border-top: none;
                padding-top: 0;
            }
        }

        > li:last-child {
            .eltd-comment {
                padding-bottom: 0;
            }
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .eltd-comments-title {
        margin-bottom: 33px;

        > * {
            margin: 0;
        }
    }

    .eltd-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 42px;
        height: 42px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .eltd-comment-text {
        padding: 0 0 0 85px;
        min-height: 42px;

        .eltd-comment-info {
            position: relative;
            display: inline-block;
            vertical-align: middle;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            position: relative;
            font-size: 18px;
            font-weight: 200;
            font-style: italic;
            font-family: $default-heading-font;
            text-transform: lowercase;
            letter-spacing: .11em;
            margin: 0 10px 0 0;

            &:after {
                content: '';
                position: absolute;
                left: 0;
                bottom: -4px;
                width: 100%;
                height: 4px;
                background-color: $first-main-color;
            }

            &:hover {
                color: $default-heading-color;
            }
        }

        .eltd-comment-name {
            margin: 0;
        }

        .eltd-comment-date {
            padding: 0 0 0 1px;
            font-size: 13px;
            font-family: $default-heading-font;
            font-weight: 200;
            color: #393939;
            position: relative;
            display: inline-block;
            vertical-align: middle;
            text-transform: lowercase;

            &:before {
                content: '/';
                font-weight: 700;
                font-size: 16px;
                color: $default-heading-color;
                padding-right: 5px;
                position: relative;
            }
        }

        .eltd-text-holder p {
            margin: 15px 0 19px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 25px;
                font-size: 16px;
                font-weight: 700;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            font-size: 18px;
            font-weight: 200;
            font-style: italic;
            font-family: $default-heading-font;
            text-transform: lowercase;
            letter-spacing: .11em;
            margin: 0 0 0 10px;
            position: relative;

            &:after {
                content: '';
                position: absolute;
                left: 0;
                bottom: -4px;
                width: 100%;
                height: 4px;
                background-color: $first-main-color;
            }

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltd-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .eltd-comment-form-inner {
        padding: 18px 0 78px;
    }
    
    .comment-reply-title {
        margin: 0 0 37px;
        text-align: center;

        .comment-reply-title-inner {
            position: relative;

            &:after {
                content: '';
                position: absolute;
                top: 50%;
                left: -55px;
                width: calc(100% + 110px);
                height: 1px;
                background-color: #ddd;
                z-index: -1;
            }
        }
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltd-comment-holder {
	    
        .eltd-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .eltd-comment-holder {
	    
        .eltd-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .eltd-comment-text {
	        
            .eltd-comment-date,
            .eltd-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .eltd-comment-holder {

        .eltd-comment-image {
            float: none;
        }

        .eltd-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltd-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.eltd-core-plugin-inactive{
    .eltd-content{
        padding-bottom: 50px;
    }
}

.post-password-form{
    label{
        width: calc(100% - 200px);
        display: inline-block;
    }

    input[type='password'] {
        margin: 10px 5px 0 0;
        padding: 20px 20px 21px;
        border-color: #000;
    }

    input[type='submit']{
        margin-left: 10px!important;
    }

    @include phone-landscape{
        label{
            width: 100%;
        }
        input[type='submit']{
            margin-top: 20px!important;
            margin-left: 0!important;
        }
    }
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include eltdButtonDefaultStyle();
	@include eltdButtonLargeParams();
	@include eltdButtonOutlineColor();
	cursor: pointer;
	
	&:hover {
		@include eltdButtonSolidHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

p.form-submit {
    text-align: center;
}

#respond textarea,
.eltd-style-form textarea,
.wpcf7-form textarea {
	display: block;
}

.eltd-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltd-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.eltd-slider-is-moving {
		
		.owl-item {
			
			.eltd-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.eltd-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-text-color;
			@include eltdTransform(translateY(-50%));
			@include eltdTransition(color .2s ease-out);
			
			&:hover {
				color: $default-text-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 20px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
            .eltd-prev-icon-double {
                left: -10px;
            }
			left: 45px;
		}
		
		.owl-next {
            .eltd-next-icon-double {
                right: -10px;
            }
			right: 45px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 16px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				margin: 0 6px;
				border-radius: 3em;
				background-color: transparent;
				border: 1px solid rgba($default-heading-color, .5);
				box-sizing: border-box;
				@include eltdTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				span {
					background-color: #393939;
					border-color: #393939;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .eltd-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltd-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            box-sizing: border-box;
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltd-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltd-slider-thumbnail-item {
                    width: 100% / ($i+2);
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.eltd-404-page {

    &.eltd-wrapper {
        padding: 0 !important;
    }

    .eltd-scrollable-widget-holder {
        display: none;
    }
    
    .eltd-content {
        height: 100vh;
	
	    .admin-bar & {
		    height: calc(100vh - 32px);
	    }
    }

    .eltd-content-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;
    }

    .eltd-page-not-found {
        position: relative;
        display: table-cell;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        padding: 0 20%;
	    text-align: center;

        .eltd-404-title {
            margin: 0;
        }
	
	    .eltd-404-subtitle {
            margin: 6px 0 0;
        }
	    
	    .eltd-404-text {
		    margin: 24px 0 0;
            padding: 0 24%;
	    }

        .eltd-btn {
            margin: 43px 0 0;
            padding: 19px 52px;
	
	        &.eltd-btn-light-style {
		        color: #585858;
		        border-color: #fff;

                .eltd-btn-cover{
                    background-color: #fff;
                }
		
		        &:hover {
			        color: #585858 !important;
			        border-color: #fff !important;

                    .eltd-btn-cover{
                        background-color: #fff!important;
                    }
		        }
	        }
        }
    }

    @include ipad-portrait {

        .eltd-page-not-found {
            padding: 40px 30px;
        }
    }
	
	@include phone-landscape {
		
		.eltd-content {
			height: auto;
			margin: 0 !important;
			
			.admin-bar & {
				height: auto;
			}
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Custom Contact Forms styles - start
   ========================================================================== */

.eltd-subscribe {
    display: block;
    width: 75%;
    margin: 0 auto;

    input[type='text'],
    input[type='email']{
        width: calc(50% - 14px);
    }

    input[type='text']{
        margin-right: 28px;
    }

    .eltd-submit-holder {
        margin-top: 24px;
    }

    @include ipad-portrait{
        width: 100%;

        input[type='text'],
        input[type='email']{
            width: 100%;
        }

        input[type='text']{
            margin-right: 0;
        }

    }
}

.eltd-submit-holder {
    position: relative;
    display: inline-block;
    text-align: center;
    margin-top: 50px;

    input.wpcf7-form-control.wpcf7-submit {
        z-index: 11;

        &:hover {
            @include eltdButtonOutlineHoverColor();
        }
    }

    .eltd-btn-background {
        @include eltdButtonBackground();
        z-index: 10;
    }

    &:hover {
        .eltd-btn-background {
            top: 10px;
            left: 10px;
        }
    }

    .ajax-loader {
        display: none !important;
    }
}

/* ==========================================================================
   #Custom Contact Forms styles - end
   ========================================================================== */


/* ==========================================================================
   #Elated Frame styles - start
   ========================================================================== */

.eltd-frame{
    background-color: #dddddd;
    position: absolute;

    &.top-left-h{
        top: 0;
        left: 0;
        height: 1px;
        width: 42px;
    }
    &.top-right-h{
        top: 0;
        right: 0;
        height: 1px;
        width: 42px;
    }
    &.bottom-right-h{
        bottom: 0;
        right: 0;
        height: 1px;
        width: 42px;
    }
    &.bottom-left-h{
        bottom: 0;
        left: 0;
        height: 1px;
        width: 42px;
    }
    &.top-left-v{
        left: 0;
        top:0;
        height: 42px;
        width: 1px;
    }
    &.bottom-left-v{
        left: 0;
        bottom:0;
        height: 42px;
        width: 1px;
    }
    &.top-right-v{
        right: 0;
        top: 0;
        height: 42px;
        width: 1px;
    }
    &.bottom-right-v{
        right: 0;
        bottom: 0;
        height: 42px;
        width: 1px;
    }
}

/* ==========================================================================
   #Elated Frame styles - end
   ========================================================================== */


/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltd-back-to-top {
    position: fixed;
    right: -33px;
    bottom: 11%;
    width: auto;
    height: auto;
    border: 1px solid #ffd94a;
    padding: 0 15px;
    background-color: #fff;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
    @include eltdTransform(rotate(-90deg));

    .eltd-back-to-top &{
        &.off {
            opacity: 0;
            visibility: hidden;
            @include eltdTransition(opacity 0.15s ease, visibility .0s .15s);
        }

        &.on {
            opacity: 1;
            visibility: visible;
            @include eltdTransition(opacity 0.3s ease);
        }
    }



    > span {
            width: auto;
            height: auto;
            border: none;
            line-height: 35px;
            background-color: transparent;
            font-size: 11.5px;
            font-family: $default-heading-font;
            font-style: italic;
            font-weight: 300;
            color: #393939;
            letter-spacing: .15em;

            &:after{
                display: none;
            }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.eltd-self-hosted-video-holder {
	
    .eltd-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */