// widgets spacing
$widget-bottom-margin: 15px;
$widget-bottom-padding: 15px;
$widget-inner-padding: 20px;

// widget color
$widget-background-color: transparent;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 5px;

// list items color
$widget-list-item-background-color: #ffffff;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: inherit;
$widget-heading-color: #1c1c1c;

// links
$widget-link-color: #000000;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 75px;
$widget-tag-cloud-height: 37px;

aside.eltd-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {

        .eltd-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.eltd-sidebar,
.wpb_widgetised_column {
    @import 'widgets/default-wordpress-widgets-flat';
    
    .widget{

        &.widget_nav_menu ul.menu {

            > li {
                padding: 13px 0;
                > a {
                    font-size: 16px;
                    font-style: normal;
                    font-family: $default-heading-font;
                    font-weight: 700;
                    color: $default-heading-color;
                    @include eltdTransition(color .2s ease-in-out);
                    text-transform: uppercase;

                    &:hover {
                        color: $widget-link-hover-color;
                    }

                    > span {
                    }

                    &.mkd-custom-menu-active {

                    }
                }

                > ul.sub-menu {
                    display: none;
                    border-top: none;
                    padding-left: 0;
                    margin-top: 17px;

                    > li {
                        overflow: hidden;

                        &.current_page_item{
                            a{
                                color: $first-main-color;
                            }
                        }

                        > a {
                            display: inline;
                            overflow: hidden;
                            padding: 0;
                            position: relative;
                            @include eltdTransition(all .2s ease-in-out);


                            &:hover{
                                color: $first-main-color;
                            }
                        }
                    }

                    .sub-menu {
                        margin-top: 22px;
                        padding-left: 15px;
                        box-sizing:border-box;

                        li{
                            a{
                                font-family: $default-heading-font;
                                font-size: 13px;
                                font-weight: 300;
                                font-style: italic;
                                color: #393939;
                            }
                        }
                    }
                }
            }

        }
        
    }
}